import classNames from 'classnames';
import React from 'react';

export interface LinearProgressProps {
  readonly color?: 'dark' | 'light';
}

export const LinearProgress: React.FC<LinearProgressProps> = ({ color = 'light' }) => {
  return (
    <div
      className={classNames('w-80 h-2 relative overflow-hidden', {
        'bg-dimmed2': color === 'light',
        'bg-inverseDimmed2': color === 'dark',
      })}
    >
      <div className="absolute w-auto left-0 top-0 bottom-0 bg-accent animate-linearIndeterminate1"></div>
      <div className="absolute w-auto left-0 top-0 bottom-0 bg-accent animate-linearIndeterminate2"></div>
    </div>
  );
};
